* {
  box-sizing: border-box;
  font-size: 14px;
  text-decoration: none;
}

:hover {
  transition-duration: .3s;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: 400;
  display: grid;
}

h2, p {
  margin: 0;
  font-weight: 400;
}

.header {
  justify-content: space-between;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  align-self: center;
  width: 40px;
  height: 40px;
}

.logo-img {
  display: block;
}

.menu-nav {
  justify-content: end;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.menu-li {
  margin-left: 20px;
  text-decoration: none;
  position: relative;
}

.menu-li:first-child {
  margin-left: 0;
}

.menu-li a {
  color: #060b35;
  text-transform: uppercase;
  text-align: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.menu-li a:hover, .menu-li a.is-active {
  color: #00acdc;
}

.menu-li a.is-active:after {
  content: "-";
  color: #0000;
  background: #00acdc;
  border-radius: 10px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.menu-li a.is-active:hover {
  color: #00acdc;
}

.cards {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.cards:hover {
  transform: scale(1.2);
}

.cards__image {
  width: 160px;
  height: 134px;
  margin: 0 auto;
}

.cards__title {
  color: #060b35;
  width: 100%;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.cards:hover .cards__title {
  color: #34568b;
}

.cards__code {
  color: #616070;
  width: 100%;
  margin-top: 4px;
  font-size: 10px;
}

.reviews {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.reviews__text {
  text-align: right;
  font-size: 10px;
}

.reviews--margin {
  margin: 16px auto 0;
}

.price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.price__text {
  color: #616070;
  font-size: 12px;
}

.price__amount {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
}

.price--margin {
  margin-top: 24px;
}

.buy-button {
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  background: #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  line-height: 40px;
}

.buy-button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.buy-button--margin {
  margin-top: 16px;
}

.stars {
  width: 100px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.cards_container {
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width <= 487px) {
  .cards_container {
    grid-template-columns: repeat(1, 200px);
  }
}

@media (width >= 488px) and (width <= 767px) {
  .cards_container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) and (width <= 1023px) {
  .cards_container {
    grid-template-columns: repeat(3, 200px);
  }
}
